

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
 .text{
     text-align: left !important;
 }
 .mysec{
  width: inherit;
  padding: 40px 0;
  
}
.mysec .col-lg-4{
  margin-top: 100px;
}
.mysec .col-lg-4 h2{
  font-weight: 700;
  font-size: 63px;
  margin-bottom: 0;
  line-height: 1;
  font-family: 'Poppins', sans-serif !important;
  white-space: nowrap;
  color: #000;
}
.mysec .col-lg-4 h3{
  font-weight: 300;
  font-size: 64px;
  line-height: 1;
  font-family: 'Poppins', sans-serif !important;
  color: #000;
}
.mysec .col-lg-4 p{
 
  font-size: 14px;
  margin-top: 30px;
  font-family: 'Poppins', sans-serif !important;
  
  color: #000;
}

.mysec .col-lg-4 .ab{
  padding: 8px 22px;
  display: inline-block;
  background: #00007C;
  color: white;
  text-decoration: none;
  margin-top: 0px;
  border: 1px solid transparent;
  transition: .5s cubic-bezier(0.785,0.135,0.15,0.86);
}
.mysec .col-lg-4 .ab:hover{
  background: white;
  color: #00007C;
  border: 1px solid #00007C;
}
.mysec .col-lg-8 img{
  width: 90%;
  position: absolute;
  top: -14rem;
  right: -11%;
}
  .container-fluid{
  height: 80vh !important;
  overflow-y: scroll !important;
  }
  .container-fluid:::-webkit-scrollbar{
    display: none !important;
  }

 .cont{
   
  width: 100vw !important;
  height: 100vh !important;
 }
 .endcall{
   background: red !important;
   color: white !important;
   align-items: center !important;
   justify-content: center !important;
 }
 @media (max-width:991.98px){
  .mysec{
    flex-direction: column-reverse !important;
  }
  .mysec .col-lg-8{
    position: relative;
    top: 0;
    right: 0;
    width: 100%;
  }

}
 .down{
   position: absolute;
   bottom: 3% !important;
   z-index: 100 !important;
   margin-left: auto !important;
   margin-right: auto !important;
   transform: translateX(7px);
   width: fit-content !important;
 }
 .MuiSvgIcon-root{
   width: 1.5em !important;
   height: 1.5em !important;
   transform: translateY(-5px);
 }
 .videofont{
   font-size: 1.5rem;
 }
 .user{
  font-family: 'Lato', sans-serif;
 }
 .fullw{
   width: 100vw;
   height: 100vh;
 }
 .authbtn{
   background: purple !important;
   color: white !important;
   outline: none !important;
   border: none !important;
 }
.butn{
    background: purple;
    color: white;
    font-weight: bold;
    font-family: sans-serif;
    padding: 32px 34px;
    border-radius: 55px;
    position: absolute;
    top: 30%;
    border: none !important;
    outline: none !important;
margin-right: 20px !important;
    left: 33%;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: center;
}
.box{
    width: 100%;
    height: 100%;
    overflow: hidden;
}
  nav{
    height: 10vh;
    background: #00007C;
    
    z-index: 2;
  }
  .google{
    font-family: sans-serif;
    font-weight: bold;
    border-radius: 25px;
  }
  .post{
    border: 2px solid white;
    width: 425px;
    height: 300px;
  }
  
  
  .links{
    display: flex;
    width: 50%;
    list-style: none;
    margin-left: auto;
    align-items: center;
    justify-content: space-around;
    height: 100%;
  }
  ul li:hover .links{
    width: 53%;
  }
  ul li:hover .a{
    transition: .5s;
    border: 2px solid white;
    border-radius: 25px;
    padding: 10px 22px;
  }
  ul li .a{
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-family: sans-serif;
    font-size: 16px;
  }
  @media (max-width:500px){
    .mysec{
      overflow: hidden !important;
      overflow-x: hidden !important;
    }
    .mysec .col-lg-4 h2{
font-size: 43px !important;
    }
    
    .mysec .col-lg-4 h3{
      font-size: 35px !important;
          }
  }
  @media (max-width:768px){
    .line{
      width: 30px;
      height: 3px;
      background: white;
      margin: 4px;
    }
    nav{
      position: relative;
    }
    .burger{
      position: absolute;
      right: 5%;
      top: 50%;
      transform: translate(-5%,-50%);
    z-index: 2;
    }
    .links{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: #00007C;
      flex-direction: column;
      clip-path: circle(100px at 90% -15%);
      -webkit-clip-path: circle(100px at 90% -15%);
      transition: all 1s ease-out;
      pointer-events: none;
  
    }
    .links.open{
      
      clip-path: circle(1000px at 90% -10%);
      -webkit-clip-path: circle(1000px at 90% -10%);
      pointer-events: all;
    }
    .links li{
      opacity: 0;
    }
    ul li .a{
      font-size: 25px;
    }
    .links li:nth-child(1){
      transition: all .5s ease .2s;
    }
    .links li:nth-child(2){
      transition: all .5s ease .4s;
    }
    .links li:nth-child(3){
      transition: all .5s ease .6s;
    }
    li.fade{
      opacity: 1 !important;
      color: white;
    }
  }